main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 100px 0 100px 0;
}

@media(max-width: 624px) {
    main {
        flex-direction: column-reverse;
    }

    .main__text {
        margin-top: 50px;
    }

    .main__img {
        width: 80%;
    }
}