.block_popularity {
    width: 180px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto 0 auto 0;
    border-radius: 30px;
    background-color: #FF6543;
    font-weight: 600;
    font-size: 21px;
}

@media(max-width: 470px) {
    .block_popularity {
        font-size: 18px;
        width: 120px;
        height: 30px;
    }
}