nav {
  margin: 50px auto 50px auto;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 200px;
  align-items: center;
}
nav .nav__link {
  text-align: start;
  color: #000;
  text-decoration: none;
  position: relative;
  font-size: 24px;
  text-align: center;
  width: 250px;
}
nav .nav__link:hover {
  text-decoration: underline;
}
nav .nav__link::before {
  background: hsl(45, 100%, 70%);
  content: "";
  inset: 0;
  position: absolute;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s ease-in-out;
  z-index: -1;
}
nav .nav__link:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}/*# sourceMappingURL=nav.module.css.map */