.link {
    padding: 10px;
    font-size: 24px;
    color: #000;
    text-decoration: none;
    display: block;
}

.post {
    color: #000;
    text-decoration: none;
    margin: 0 auto 0 auto;
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

@media(min-width: 800px) {
    .post {
        max-width: 80%;
    }
}

.bug {
    .bug__title {
        margin-top: 100px;
        display: block;
        font-weight: 600;
        font-size: 25px;
        text-align: center;
    }

    .bug__subtitle {
        margin-top: 10px;
        display: block;
        font-weight: 400;
        font-size: 18px;
        text-align: center;
        color: rgb(38, 181, 247);
    }
}

//search

.search {
    margin: 50px auto 0 auto;
    width: 80%;
    max-width: 500px;
    height: 40px;
    border: 1px solid #000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    padding: 0 10px 0 10px;

    // .search__logo {
    //     width: 40px;
    //     height: 40px;
    // }

    .search__input {
        border: none;

        &:focus {
            color: #212529;
            background-color: #fff;
            border-color: #ffffff;
            outline: 0;
          }

        &::placeholder {
            color: #000000;
            opacity: 0.4;
            font-size: 16px;
        }
    }
}