.post {
  padding: 30px;
}
.post .post__title {
  font-size: 26px;
  font-weight: 500;
}
.post .post__text {
  padding: 10px;
  font-size: 18px;
  font-weight: 300;
}
.post .post__error {
  text-align: center;
  font-size: 36px;
  font-weight: 500;
  margin-top: 20%;
}
.post .post__link {
  display: block;
  margin-top: 20px;
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  text-decoration: none;
  color: rgb(38, 181, 247);
}
.post .post__image {
  margin: 50px 0 50px 0;
  width: 80%;
  max-width: 500px;
}

@media (max-width: 600px) {
  .post__image {
    margin-left: 10% !important;
    margin-right: 10% !important;
  }
}/*# sourceMappingURL=post.module.css.map */